import React, { useEffect, useState } from 'react';
import { graphql, useStaticQuery } from 'gatsby';

import SEO from '../components/seo';
import classNames from 'classnames/bind';
import { useBreakpoints } from '../hooks/breakpoints';
// import { news, newsItem, small, image } from './news.module.scss';
import * as styles from './news.module.scss';
const cx = classNames.bind(styles);
import NewsListItem from '../components/newsListItem';
import { createRows } from '../utils/row-helper';

const News: React.FC = () => {
  const {
    mpapi: {
      newsItems: { data: newsItems },
    },
  } = useStaticQuery<GatsbyTypes.NewsItemsQuery>(graphql`
    query NewsItems {
      mpapi {
        newsItems(pagination: { limit: 100 }, sort: "startDate:desc") {
          data {
            attributes {
              slug
              title
              images(pagination: { limit: 1 }) {
                data {
                  attributes {
                    alternativeText
                    height
                    width
                    url
                    formats
                    hash
                  }
                }
              }
              startDate
              endDate
            }
          }
        }
      }
    }
  `);

  if (!newsItems) return;

  const [firstNewsItem, ...otherNewsItems] = newsItems;

  const [columns, setColumns] = useState([]);
  const isDesktop = useBreakpoints();

  useEffect(() => {
    createRows(otherNewsItems, isDesktop, setColumns, 3);
  }, [isDesktop]);

  return (
    <>
      <SEO
        title={'News | Atelier Mendgen Peissl'}
        description="Latest news on architects Anna Mendgen und Dirk Peissl."
        image="https://mendgenpeissl.com/logo.jpg"
      />

      <h1>News</h1>

      {firstNewsItem && (
        <ul className={cx(['newsList', 'firstNewsItem'])}>
          <NewsListItem
            key={firstNewsItem.attributes.slug}
            newsItem={firstNewsItem.attributes}
          />
        </ul>
      )}

      <section className={cx('news')}>
        {columns.map((newsItems, i) => (
          <ul className={cx('newsList')} key={i}>
            {newsItems.map((newsItem) => (
              <NewsListItem key={newsItem.slug} newsItem={newsItem} />
            ))}
          </ul>
        ))}
      </section>
    </>
  );
};

export default News;
