import * as React from 'react';

import { Link, navigate } from 'gatsby';

import { ImageType } from '../interfaces/image.interface';
import classNames from 'classnames/bind';
import * as styles from './newsListItem.module.scss';
import Image from './image';
import Moment from 'react-moment';

const cx = classNames.bind(styles);

type ImageResponse = {
  data: [
    {
      attributes: ImageType;
    }
  ];
};

type NewsItem = {
  title: string;
  slug: string;
  images: ImageResponse;
  startDate: string;
  endDate: string;
};

type Props = {
  newsItem: NewsItem;
};

const NewsListItem: React.FC<Props> = ({
  newsItem: { title, slug, startDate, endDate, images },
}) => {
  return (
    <li className={cx('newsItemStyle')}>
      {images.data[0] && (
        <Image
          className={cx('imageStyle')}
          image={images.data[0].attributes}
          alt={title}
          onClick={() => navigate(`/news/${slug}`)}
        />
      )}
      <Link className={cx('linkStyle')} to={`/news/${slug}`}>
        <h3 className={cx('title')}>{title}</h3>
        <p className={cx('small')}>
          <Moment format="MMMM Do YYYY">{startDate}</Moment>
          {endDate && (
            <span>
              {' '}
              until <Moment format="MMMM Do YYYY">{endDate}</Moment>
            </span>
          )}
        </p>
      </Link>
    </li>
  );
};

export default NewsListItem;
